import React from "react";
import Column from "./Column.js";
import classNames from "classnames";

class Header extends React.Component {
  render() {
    const style = classNames({
      "bg-blue-500 flex justify-between shadow-lg ": true,
      rounded: !this.props.isOpen,
      "border-b border-blue-900": this.props.isOpen
    });
    const arrow = classNames({
      "w-4 ": true,
      "arrow-up": !this.props.isOpen,
      "arrow-down": this.props.isOpen
    });
    return (
      <div className={style}>
        <div className="p-1">
          <input
            className="shadow rounded appearance-none border py-2 px-3 text-gray-500 leading-tight focus:outline-none focus:shadow-outline focus:bg-white"
            id="cognome"
            type="text"
            placeholder="Contenitore"
          />
        </div>
        <div className="flex">
          <ul className="flex">
            <li
              onClick={info =>
                this.props.manageContainer({
                  indexContainer: this.props.index,
                  operation: "copy"
                })
              }
              className="shadow bg-blue-800 cursor-pointer flex w-12 justify-center items-center py-2 px-3 mr-1 hover:bg-blue-700"
            >
              <div className="">
                <img
                  className="w-6"
                  src="./Img/icon/copy_white.png"
                  alt="bin"
                />
              </div>
            </li>
            <li
              onClick={info =>
                this.props.manageContainer({
                  indexContainer: this.props.index,
                  operation: "delete"
                })
              }
              className="shadow bg-blue-800 cursor-pointer flex w-12 justify-center items-center py-2 px-3 mr-1 hover:bg-blue-700"
            >
              <div className="">
                <img className="w-6" src="./Img/icon/bin_white.png" alt="bin" />
              </div>
            </li>
            <li
              onClick={info =>
                this.props.manageContainer({
                  indexContainer: this.props.index,
                  operation: "open"
                })
              }
              className="shadow bg-blue-800 cursor-pointer w-12 flex items-center justify-center  py-2 px-3 hover:bg-blue-700"
            >
              <div className="">
                <img
                  className={arrow}
                  src="./Img/arrow_down_white.png"
                  alt="arrow down"
                />
              </div>
            </li>
          </ul>
        </div>
      </div>
    );
  }
}

class Body extends React.Component {
  render() {
    const style = classNames({
      "bg-white  text-center border border-gray-400": true,
      "container-closed": !this.props.isOpen,
      "pb-2 container-open": this.props.isOpen
    });

    let Colonne = [];
    for (let i in this.props.containerInfo.column) {
      /*  console.log("array di valori:" + this.props.containerInfo.column[i]); */
      Colonne.push(
        <Column
          key={i}
          indexContainer={this.props.indexContainer}
          selectModal={info => this.props.selectModal(info)}
          manageColumn={info => this.props.manageColumn(info)}
          manageColumnElements={info => this.props.manageColumnElements(info)}
          fract={this.props.containerInfo.column[i].fract}
          context={this.props.containerInfo.column[i].context}
          index={i}
        />
      );
    }

    return (
      <div className={style}>
        <div className="py-4 px-4">
          <span className="text-gray-500 ">
            Per aggiungere elementi, Ã¨ innanzitutto necessario aggiungere una
            colonna
          </span>

          <div className="flex flex-wrap">
            {/* QUI INSERIRO' LE VARIE COLONNE DEL MIO CONTENITORE*/}
            {Colonne}
          </div>
        </div>
        <div className="flex justify-end">
          <button
            onClick={() =>
              this.props.selectModal({ selectedModal: "SuggestedLayout" })
            }
            className="bg-transparent hover:bg-blue-500 text-blue-700 font-semibold hover:text-white py-2 px-4 border border-blue-500 hover:border-transparent rounded mr-1"
          >
            + Contenitore
          </button>
          <button
            onClick={info =>
              this.props.manageColumn({
                indexContainer: this.props.indexContainer,
                operation: "add"
              })
            }
            className="bg-transparent hover:bg-blue-500 text-blue-700 font-semibold hover:text-white py-2 px-4 border border-blue-500 hover:border-transparent rounded mr-1"
          >
            + Colonna
          </button>
          <button></button>
        </div>
      </div>
    );
  }
}

class Container extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      column: "1/1"
    };
  }
  componentDidMount() {
    let layout = this.props.choosedLayout ? this.props.choosedLayout : "1/1";
    this.setState({
      column: layout
    });
  }

  componentDidUpdate(prevProps) {
    // Typical usage (don't forget to compare props):
    if (this.props.containerInfo.column !== prevProps.containerInfo.column) {
      this.setState({ column: this.props.containerInfo.column });
    }
  }

  // funzione per gestire le colonne del container, per aggiungerle/toglierle etc
  handleColumn(index, value, action) {}

  render() {
    return (
      <div className="mt-4 ">
        <Header
          isOpen={this.props.isOpen}
          containerInfo={this.props.containerInfo}
          manageContainer={info => this.props.manageContainer(info)}
          index={this.props.index}
        />
        <Body
          isOpen={this.props.isOpen}
          manageColumn={info => this.props.manageColumn(info)}
          manageColumnElements={info => this.props.manageColumnElements(info)}
          indexContainer={this.props.index}
          containerInfo={this.props.containerInfo}
          selectModal={info => this.props.selectModal(info)}
        />
      </div>
    );
  }
}

export default Container;
