import React from "react";
/* import SuggestedLayout from "./SuggestedLayout.js";
import ChooseElement from "./ChooseElement.js"; */
import Container from "./Container.js";
/* import classNames from "classnames"; */
import ModalManager from "./ModalManager.js";
//funzione per il DeepCopy
import { clone } from "./utils.js";
/* import TitleText from "./TitleText.js"; */
/* import * as R from "ramda"; */
//import { CSSTransition } from "react-transition-group";

class StartingPage extends React.Component {
  render() {
    return (
      <div className="mt-2 border-dashed border border-gray-500 p-5">
        <div className="flex justify-center flex-col items-center font-worksans">
          <span className="font-bold">
            Per iniziare, aggiungere un contenitore o aggiungere una pagina
            precompilata.
          </span>
          <span className="text-gray-500 italic text-center text-sm">
            Il processo di costruzione inizia sempre con un contenitore, poi
            colonne, poi elementi.
          </span>
          <button
            onClick={info =>
              this.props.selectModal({ selectedModal: "SuggestedLayout" })
            }
            className="mt-4 bg-transparent hover:bg-blue-500 text-blue-700 font-semibold hover:text-white py-2 px-4 border border-blue-500 hover:border-transparent rounded"
          >
            + Contenitore
          </button>
        </div>
      </div>
    );
  }
}

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      displayChooseLayout: false,
      displayChooseElement: false,
      showModal: false,
      modalSelected: null,
      container: []
    };
  }

  /* ########## GESTIONE CONTENUTO COLONNE ########################### */
  manageColumnElements(info) {
    console.log(
      "sei passato da manageColumnElements con info:" + JSON.stringify(info)
    );
    let array = clone([], this.state.container);
    switch (info.operation) {
      case "add":
        array[info.indexContainer].column[info.indexColonna].context.push(
          info.value
        );
        break;
      case "delete":
        array[info.indexContainer].column[info.indexColonna].context.splice(
          info.indexElement,
          1
        );
        break;
      // swappare elemento colonna
      case "swap":
        console.log("sei nello swap element !");
        //controllo Swap per capire se Ã¨ l'ultimo o il primo elemento
        if (info.swap === "before" && info.indexElement === "0") {
          break;
        } else if (
          info.swap === "after" &&
          parseInt(info.indexElement) ===
            parseInt(
              array[info.indexContainer].column[info.indexColonna].context
                .length - 1
            )
        ) {
          break;
        }

        // inserire condizione di swap

        if (info.swap === "before") {
          [
            array[info.indexContainer].column[info.indexColonna].context[
              info.indexElement - 1
            ],
            array[info.indexContainer].column[info.indexColonna].context[
              info.indexElement
            ]
          ] = [
            array[info.indexContainer].column[info.indexColonna].context[
              info.indexElement
            ],
            array[info.indexContainer].column[info.indexColonna].context[
              info.indexElement - 1
            ]
          ];
        } else if (info.swap === "after") {
          [
            array[info.indexContainer].column[info.indexColonna].context[
              info.indexElement
            ],
            array[info.indexContainer].column[info.indexColonna].context[
              parseInt(info.indexElement) + 1
            ]
          ] = [
            array[info.indexContainer].column[info.indexColonna].context[
              parseInt(info.indexElement) + 1
            ],
            array[info.indexContainer].column[info.indexColonna].context[
              info.indexElement
            ]
          ];
        }

        break;
      case "copy":
        let item =
          array[info.indexContainer].column[info.indexColonna].context[
            info.indexElement
          ];
        array[info.indexContainer].column[info.indexColonna].context.splice(
          info.indexElement,
          0,
          item
        );
        break;
      case "update":
        array[info.indexContainer].column[info.indexColonna].context[
          info.indexElement
        ] = info.value;
        break;
      default:
    }
    this.setState({
      container: array
    });
  }

  /* ########## GESTIONE COLONNE ########################### */

  manageColumn(info) {
    console.log("sei passato da manageColumn con info:" + JSON.stringify(info));
    let array = [];
    // DEEP COPY dello state
    array = clone(array, this.state.container);
    switch (info.operation) {
      case "add":
        array[info.indexContainer].column.push({
          fract: "1/6",
          context: []
        });
        break;
      case "delete":
        array[info.indexContainer].column.splice(info.indexColonna, 1);
        break;
      case "copy":
        // ATTENZIONE ! il metodo .slice() fa una copia superficiale,
        // gli elementi annidati devono essere copiati con R.clone() o Clone()
        let item = array[info.indexContainer].column[info.indexColonna];
        array[info.indexContainer].column.splice(info.indexColonna, 0, item);
        break;
      case "update":
        array[info.indexContainer].column[info.indexColonna].fract = info.value;
        break;

      // swappare colonna
      case "swap":
        console.log("sei nello swap !");
        //controllo Swap per capire se Ã¨ l'ultimo o il primo elemento
        if (info.swap === "before" && info.indexColonna === "0") {
          break;
        } else if (
          info.swap === "after" &&
          parseInt(info.indexColonna) ===
            parseInt(array[info.indexContainer].column.length - 1)
        ) {
          break;
        }

        // inserire condizione di swap
        if (info.swap === "before") {
          [
            array[info.indexContainer].column[info.indexColonna - 1],
            array[info.indexContainer].column[info.indexColonna]
          ] = [
            array[info.indexContainer].column[info.indexColonna],
            array[info.indexContainer].column[info.indexColonna - 1]
          ];
        } else if (info.swap === "after") {
          [
            array[info.indexContainer].column[info.indexColonna],
            array[info.indexContainer].column[parseInt(info.indexColonna) + 1]
          ] = [
            array[info.indexContainer].column[parseInt(info.indexColonna) + 1],
            array[info.indexContainer].column[info.indexColonna]
          ];
        }
        break;
      default:
    }
    this.setState({
      container: array
    });
  }

  /* ########## GESTIONE CONTAINER ########################### */

  // scelto un layout di partenza da un utente creo un container con quel criterio
  newContainer(layout) {
    console.log("layout selezionato" + layout);
    let array = layout.split(",");
    array = array.map(element => {
      return { fract: element, context: [] };
    });
    // Creo un Object per il Container che conterrÃ  un array di Obj per ogni colonna
    let obj = {
      column: array,
      name: "contenitore",
      open: true
    };
    let array2 = clone([], this.state.container);
    array2.push(obj);
    this.setState({
      showModal: false,
      modalSelected: null,
      modalInfo: null,
      displayChooseElement: false,
      container: array2
    });
  }
  manageContainer(info) {
    console.log(
      "sei passato da manageContainer con info:" + JSON.stringify(info)
    );
    // DEEP COPY dello state
    let array = clone([], this.state.container);
    switch (info.operation) {
      case "delete":
        array.splice(info.indexContainer, 1);
        break;
      case "copy":
        // ATTENZIONE ! il metodo .slice() fa una copia superficiale,
        // gli elementi annidati devono essere copiati con R.clone()
        array.splice(info.indexContainer, 0, array[info.indexContainer]);
        break;
      case "open":
        array[info.indexContainer].open = !this.state.container[
          info.indexContainer
        ].open;
        break;
      case "update":
        // da fare in futuro
        break;
      case "add":
        //da fare in futuro
        break;
      default:
    }
    this.setState({
      container: array
    });
  }

  selectModal(info) {
    this.setState({
      showModal: true,
      modalSelected: info.selectedModal,
      modalInfo: info.info
    });
  }
  closeModal() {
    this.setState({
      showModal: false,
      modalSelected: null,
      modalInfo: null
    });
  }

  //funzione per renderizzare i container
  renderContainer() {
    let contenitore = [];
    for (let i in this.state.container) {
      contenitore.push(
        <Container
          key={i}
          manageColumn={info => this.manageColumn(info)}
          manageContainer={info => this.manageContainer(info)}
          manageColumnElements={info => this.manageColumnElements(info)}
          isOpen={this.state.container[i].open}
          index={i}
          containerInfo={this.state.container[i]}
          selectModal={info => this.selectModal(info)}
        />
      );
    }
    return contenitore;
  }

  render() {
    let containers = this.renderContainer();
    return (
      <div className="p-4 bg-gray-200 h-screen overflow-auto">
        {this.state.container.length === 0 ? (
          <StartingPage selectModal={info => this.selectModal(info)} />
        ) : null}

        {this.state.showModal ? (
          <ModalManager
            modalInfo={this.state.modalInfo}
            manageColumnElements={info => this.manageColumnElements(info)}
            selectModal={info => this.selectModal(info)}
            modalSelected={this.state.modalSelected}
            closeModal={() => this.closeModal()}
            setLayout={a => this.newContainer(a)}
          />
        ) : null}
        {containers}
      </div>
    );
  }
}

export default App;
