export const getProducts = (cb, requestedUrl) => {
  if (!requestedUrl) {
    return null;
  }

  fetch(requestedUrl)
    .then(response => {
      if (!response.ok) {
        throw Error(response.statusText);
      }
      return response.json();
    })
    .then(jsonData => {
      // console.log(jsonData);
      cb(jsonData);
    })
    .catch(error => {
      console.error("getProductserror:");
      console.error(error);
    });
};

export const countItems = array => {
  return array
    .map(element => {
      return parseInt(element.numbers);
    })
    .reduce((results, currentValue) => {
      return results + currentValue;
    }, 0);
  //console.log("il conteggio Ã¨:" +x);
};

// Funzione che fa una DeepCopy dell'array
export const clone = (target, source) => {
  for (let key in source) {
    // Use getOwnPropertyDescriptor instead of source[key] to prevent from triggering setter/getter.
    let descriptor = Object.getOwnPropertyDescriptor(source, key);
    //
    //console.log("Ti piace utils?");
    //In caso di Obj o elementi vuoti(senza attributi) esco dal for
    if (!descriptor) {
      console.log("Undefined=morte");
      break;
    }
    if (descriptor.value instanceof String) {
      target[key] = new String(descriptor.value);
    } else if (descriptor.value instanceof Array) {
      target[key] = clone([], descriptor.value);
    } else if (descriptor.value instanceof Object) {
      let prototype = Reflect.getPrototypeOf(descriptor.value);
      let cloneObject = clone({}, descriptor.value);
      Reflect.setPrototypeOf(cloneObject, prototype);
      target[key] = cloneObject;
    } else {
      Object.defineProperty(target, key, descriptor);
    }
  }
  let prototype = Reflect.getPrototypeOf(source);
  Reflect.setPrototypeOf(target, prototype);
  return target;
};
