import React from "react";
import {
  EditorState,
  ContentState,
  convertToRaw
  /*, convertFromRaw */
} from "draft-js";
/* import { Editor } from "react-draft-wysiwyg"; */
import draftToHtml from "draftjs-to-html";
import htmlToDraft from "html-to-draftjs";
import draftToMarkdown from "draftjs-to-markdown";
import EditorConvertToHTML from "./EditorConvertToHTML.js";

class InputText extends React.Component {
  constructor(props) {
    super(props);
    this.state = { editorState: EditorState.createEmpty() };
  }

  onEditorStateChange: Function = editorState => {
    this.setState({
      editorState
    });
  };

  componentDidMount() {
    let field = !this.props.modalInfo.info
      ? EditorState.createEmpty()
      : EditorState.createWithContent(
          ContentState.createFromBlockArray(
            htmlToDraft(this.props.modalInfo.info.value)
          )
        );
    this.setState({
      editorState: field
    });
  }

  componentDidUpdate(prevProps) {
    // Typical usage (NEVER forget to compare props):
    if (this.props.modalInfo.info !== prevProps.modalInfo.info) {
      this.setState({
        // Sintassi per convertire una stringa HTML e aggiornare lo state nel Draft
        editorState: EditorState.createWithContent(
          ContentState.createFromBlockArray(
            htmlToDraft(this.props.modalInfo.info.value)
          )
        )
      });
    }
  }

  updateSend() {
    const { editorState } = this.state;
    const add = (
      <button
        onClick={info => {
          this.props.manageColumnElements({
            operation: "add",
            indexContainer: this.props.modalInfo.indexContainer,
            indexColonna: this.props.modalInfo.indexColonna,
            value: {
              type: "InputText",
              // da aggiungere per fare il copy
              value: draftToHtml(convertToRaw(editorState.getCurrentContent())),
              text: draftToMarkdown(
                convertToRaw(editorState.getCurrentContent())
              )
            }
          });
          this.props.closeModal();
        }}
        className="mt-4 bg-transparent hover:bg-blue-500 text-blue-700 font-semibold hover:text-white py-2 px-4 border border-blue-500 hover:border-transparent rounded"
      >
        Invia
      </button>
    );

    const update = (
      <button
        onClick={
          /*   () =>
            console.log(
              "ecco l element index:" + this.props.modalInfo.indexElement
            ) */
          info => {
            this.props.manageColumnElements({
              operation: "update",
              indexContainer: this.props.modalInfo.indexContainer,
              indexColonna: this.props.modalInfo.indexColonna,
              indexElement: this.props.modalInfo.indexElement,
              value: {
                type: "InputText",
                // da aggiungere per fare il copy
                value: draftToHtml(
                  convertToRaw(editorState.getCurrentContent())
                ),
                text: draftToMarkdown(
                  convertToRaw(editorState.getCurrentContent())
                )
              }
            });
            this.props.closeModal();
          }
        }
        className="mt-4 bg-transparent hover:bg-blue-500 text-blue-700 font-semibold hover:text-white py-2 px-4 border border-blue-500 hover:border-transparent rounded"
      >
        Aggiorna
      </button>
    );

    return !this.props.modalInfo.info ? add : update;
  }

  render() {
    const button = this.updateSend();
    const { editorState } = this.state;
    return (
      <div className=" lg:w-3/4">
        <div className="">
          <div className="bg-blue-500 py-2 border-b border-blue-600 shadow-lg">
            <h1 className="text-white text-center font-bold uppercase ">
              Inserisci il testo
            </h1>
          </div>
          <div className="bg-white p-4">
            <div>
              <EditorConvertToHTML
                editorState={editorState}
                onEditorStateChange={this.onEditorStateChange}
              />
              <div className="flex justify-between">
                {button}
                <button
                  onClick={() => this.props.closeModal()}
                  className="mt-4 bg-transparent hover:bg-blue-500 text-blue-700 font-semibold hover:text-white py-2 px-4 border border-blue-500 hover:border-transparent rounded"
                >
                  Annulla
                </button>
              </div>
            </div>
            <div></div>
          </div>
        </div>
      </div>
    );
  }
}

export default InputText;
