import React from "react";
import classNames from "classnames";
import Elements from "./Elements.js";

class FractSquare extends React.Component {
  render() {
    let squarestyle = classNames({
      "border cursor-pointer border-gray-300 hover:bg-blue-300 bg-white w-8 h-8 flex items-center justify-center": true,
      "bg-blue-300 hiver:bg-blue-400":
        this.props.fract === this.props.activefract
    });
    return (
      <div
        onClick={() => this.props.setFract(this.props.fract)}
        className={squarestyle}
      >
        {this.props.fract}
      </div>
    );
  }
}

class FractSelection extends React.Component {
  render() {
    return (
      <div className="absolute flex left-0 top-0 bg-red-300 z-10">
        {this.props.children}
      </div>
    );
  }
}

class Column extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      fract: null,
      showFractSelect: false
    };
  }
  componentDidUpdate(prevProps) {
    // Typical usage (don't forget to compare props):
    if (this.props.fract !== prevProps.fract) {
      this.setState({ fract: this.props.fract });
    }
  }

  handleFract(value) {
    console.log("ho chiamato handleFract per cambiare il valore della colonna");
    this.props.manageColumn({
      indexContainer: this.props.indexContainer,
      indexColonna: this.props.index,
      value: value,
      operation: "update"
    });
    this.setState({
      fract: value
    });
  }

  openFractMenu() {
    this.setState({
      showFractSelect: !this.state.showFractSelect
    });
  }
  componentDidMount() {
    this.setState({
      fract: this.props.fract
    });
  }

  render() {
    let value = this.state.fract === "1/1" ? "full" : this.state.fract;
    let style = classNames({
      [`w-${value}`]: true
    });

    let buttonColumn = classNames({
      "w-6 h-6 hover:bg-gray-500 cursor-pointer  bg-gray-200 p-1 border-b border-r border-gray-600 flex items-center justify-center": true
    });
    let arrow = classNames({
      "w-6 h-6 hover:opacity-100 cursor-pointer opacity-75 p-1  flex items-center justify-center": true
    });
    let Elem = [];
    for (let i in this.props.context) {
      Elem.push(
        <Elements
          key={i}
          indexContainer={this.props.indexContainer}
          indexColonna={this.props.index}
          indexElements={i}
          manageColumnElements={info => this.props.manageColumnElements(info)}
          selectModal={info => this.props.selectModal(info)}
          context={this.props.context[i]}
        />
      );
    }
    return (
      <div className={style}>
        <div className=" sm:p-2">
          <div className="border border-gray-400">
            <div className="flex flex-col sm:flex-row items-center justify-between">
              <div className="flex">
                <div
                  onClick={() => this.openFractMenu()}
                  className="w-6 h-6 relative hover:bg-gray-500 cursor-pointer  bg-gray-200 p-1 border-b border-r border-gray-600 flex items-center justify-center"
                >
                  {this.state.fract}
                  {this.state.showFractSelect ? (
                    <FractSelection>
                      <div>
                        <div className="flex">
                          <FractSquare
                            fract="1/6"
                            activefract={this.state.fract}
                            setFract={data => this.handleFract(data)}
                          />
                          <FractSquare
                            fract="1/5"
                            activefract={this.state.fract}
                            setFract={data => this.handleFract(data)}
                          />
                          <FractSquare
                            fract="1/4"
                            activefract={this.state.fract}
                            setFract={data => this.handleFract(data)}
                          />
                          <FractSquare
                            fract="1/3"
                            activefract={this.state.fract}
                            setFract={data => this.handleFract(data)}
                          />
                          <FractSquare
                            fract="2/5"
                            activefract={this.state.fract}
                            setFract={data => this.handleFract(data)}
                          />
                          <FractSquare
                            fract="1/2"
                            activefract={this.state.fract}
                            setFract={data => this.handleFract(data)}
                          />
                        </div>
                        <div className="flex">
                          <FractSquare
                            fract="3/5"
                            activefract={this.state.fract}
                            setFract={data => this.handleFract(data)}
                          />
                          <FractSquare
                            fract="2/3"
                            activefract={this.state.fract}
                            setFract={data => this.handleFract(data)}
                          />
                          <FractSquare
                            fract="3/4"
                            activefract={this.state.fract}
                            setFract={data => this.handleFract(data)}
                          />
                          <FractSquare
                            fract="4/5"
                            activefract={this.state.fract}
                            setFract={data => this.handleFract(data)}
                          />
                          <FractSquare
                            fract="5/6"
                            activefract={this.state.fract}
                            setFract={data => this.handleFract(data)}
                          />
                          <FractSquare
                            fract="1/1"
                            activefract={this.state.fract}
                            setFract={data => this.handleFract(data)}
                          />
                        </div>
                      </div>
                    </FractSelection>
                  ) : null}
                </div>
                <div
                  onClick={info =>
                    this.props.manageColumn({
                      indexContainer: this.props.indexContainer,
                      indexColonna: this.props.index,
                      operation: "copy"
                    })
                  }
                  className={buttonColumn}
                >
                  <img
                    className="w-3 opacity-50"
                    src="./Img/icon/copy_black.png"
                    alt="copy"
                  />
                </div>

                <div
                  onClick={info =>
                    this.props.manageColumn({
                      indexContainer: this.props.indexContainer,
                      indexColonna: this.props.index,
                      operation: "delete"
                    })
                  }
                  className={buttonColumn}
                >
                  <img
                    className="w-3 opacity-50"
                    src="./Img/icon/bin_black.png"
                    alt="bin"
                  />
                </div>
              </div>
              <div className="flex">
                <div
                  onClick={info =>
                    this.props.manageColumn({
                      indexContainer: this.props.indexContainer,
                      indexColonna: this.props.index,
                      operation: "swap",
                      swap: "before"
                    })
                  }
                  className={arrow}
                >
                  <img
                    className="w-3 opacity-50"
                    src="./Img/icon/arrow_left.png"
                    alt="arrow left"
                  />
                </div>

                <div
                  onClick={info =>
                    this.props.manageColumn({
                      indexContainer: this.props.indexContainer,
                      indexColonna: this.props.index,
                      operation: "swap",
                      swap: "after"
                    })
                  }
                  className={arrow}
                >
                  <img
                    className="w-3 opacity-50"
                    src="./Img/icon/arrow_right.png"
                    alt="arrow right"
                  />
                </div>
              </div>
            </div>
            <div className="p-4">
              {Elem}
              <div
                onClick={info =>
                  this.props.selectModal({
                    selectedModal: "ChooseElement",
                    info: {
                      indexContainer: this.props.indexContainer,
                      indexColonna: this.props.index
                    }
                  })
                }
                className="cursor-pointer flex flex-col border-dashed border border-gray-500 p-1 flex items-center justify-center"
              >
                <span className="text-gray-600 text-xs md:text-base lg:text-xl">
                  <span className="font-bold text-xl">+</span> Aggiungere una
                  classe all'elemento wrapping HTML
                </span>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Column;
