import React from "react";

class IconElement extends React.Component {
  render() {
    let url = "./Img/icon/" + this.props.url;
    return (
      <div
        onClick={info =>
          this.props.selectModal({
            selectedModal: this.props.text,
            info: this.props.modalInfo
          })
        }
        className="p-2 cursor-pointer border border-gray-700 font-bold flex items-center shadow-lg"
      >
        <img className="w-6" src={url} alt={this.props.text} />
        <h1 className="ml-2">{this.props.text}</h1>
      </div>
    );
  }
}

class ChooseElement extends React.Component {
  render() {
    return (
      <div>
        <div className="rounded z-50 absolute p-4 left-0 w-full">
          <div className="">
            <div className="bg-blue-500 py-2 border-b border-blue-600 shadow-lg">
              <h1 className="text-white text-center font-bold uppercase ">
                Seleziona un elemento
              </h1>
            </div>
            <div className="rounded-b bg-white p-4 ">
              <div className="mt-2">
                <div className="mt-2 grid-elements  text-black">
                  <IconElement
                    modalInfo={this.props.modalInfo}
                    selectModal={info => this.props.selectModal(info)}
                    text="Immagine"
                    url="picture_black.svg"
                  />
                  <IconElement
                    modalInfo={this.props.modalInfo}
                    selectModal={info => this.props.selectModal(info)}
                    text="Titolo"
                    url="title.svg"
                  />
                  <IconElement
                    modalInfo={this.props.modalInfo}
                    selectModal={info => this.props.selectModal(info)}
                    text="Blocco di testo"
                    url="text.png"
                  />
                  <IconElement
                    modalInfo={this.props.modalInfo}
                    selectModal={info => this.props.selectModal(info)}
                    text="CheckList"
                    url="checklist_black.svg"
                  />
                  <IconElement
                    modalInfo={this.props.modalInfo}
                    selectModal={info => this.props.selectModal(info)}
                    text="Accordion"
                    url="accordion.png"
                  />
                </div>
                <div className="flex justify-end">
                  <button
                    onClick={() => this.props.closeModal()}
                    className="mt-4 bg-transparent hover:bg-blue-500 text-blue-700 font-semibold hover:text-white py-2 px-4 border border-blue-500 hover:border-transparent rounded"
                  >
                    Annulla
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default ChooseElement;
