import React from "react";
import classNames from "classnames";

class InsertImage extends React.Component {
  constructor(props) {
    super(props);
    this.state = { value: "" };
  }

  handleNameChange = event => {
    this.setState({ value: event.target.value });
  };

  componentDidMount() {
    let field = !this.props.modalInfo.info ? "" : this.props.modalInfo.info.url;
    this.setState({
      value: field
    });
  }

  componentDidUpdate(prevProps) {
    // Typical usage (NEVER forget to compare props):
    if (this.props.modalInfo.info !== prevProps.modalInfo.info) {
      this.setState({ value: this.props.modalInfo.info.url });
    }
  }
  updateSend() {
    const add = (
      <button
        onClick={info => {
          this.props.manageColumnElements({
            operation: "add",
            indexContainer: this.props.modalInfo.indexContainer,
            indexColonna: this.props.modalInfo.indexColonna,
            value: {
              type: "InsertImage",
              url: this.state.value
            }
          });
          this.props.closeModal();
        }}
        className="mt-4 bg-transparent hover:bg-blue-500 text-blue-700 font-semibold hover:text-white py-2 px-4 border border-blue-500 hover:border-transparent rounded"
      >
        Invia
      </button>
    );

    const update = (
      <button
        onClick={info => {
          this.props.manageColumnElements({
            operation: "update",
            indexContainer: this.props.modalInfo.indexContainer,
            indexColonna: this.props.modalInfo.indexColonna,
            indexElement: this.props.modalInfo.indexElement,
            value: {
              type: "InsertImage",
              url: this.state.value
            }
          });
          this.props.closeModal();
        }}
        className="mt-4 bg-transparent hover:bg-blue-500 text-blue-700 font-semibold hover:text-white py-2 px-4 border border-blue-500 hover:border-transparent rounded"
      >
        Aggiorna
      </button>
    );
    return !this.props.modalInfo.info ? add : update;
  }

  render() {
    const style = classNames({
      "shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline": true
    });
    let button = this.updateSend();
    return (
      <div className="w-full lg:w-3/4">
        <div className="">
          <div className="bg-blue-500 py-2 border-b border-blue-600 shadow-lg">
            <h1 className="text-white text-center font-bold uppercase ">
              Inserisci l'URL dell'immagine{" "}
            </h1>
          </div>
          <div className="bg-white p-4">
            <div>
              <input
                className={style}
                type="text"
                value={this.state.value}
                onChange={this.handleNameChange}
              />
              <div className="flex justify-between">
                {button}
                <button
                  onClick={() => this.props.closeModal()}
                  className="mt-4 bg-transparent hover:bg-blue-500 text-blue-700 font-semibold hover:text-white py-2 px-4 border border-blue-500 hover:border-transparent rounded"
                >
                  Annulla
                </button>
              </div>
            </div>
            <div></div>
          </div>
        </div>
      </div>
    );
  }
}

export default InsertImage;
