import React from "react";
import SuggestedLayout from "./Modals/SuggestedLayout.js";
import ChooseElement from "./Modals/ChooseElement.js";
import InputText from "./Modals/InputText.js";
import TitleText from "./Modals/TitleText.js";
import InsertImage from "./Modals/InsertImage.js";

class ModalManager extends React.Component {
  chooseModal(info) {
    switch (info) {
      case "SuggestedLayout":
        return (
          <SuggestedLayout
            closeModal={() => this.props.closeModal()}
            setLayout={a => this.props.setLayout(a)}
          />
        );

      case "ChooseElement":
        return (
          <ChooseElement
            modalInfo={this.props.modalInfo}
            selectModal={info => this.props.selectModal(info)}
            closeModal={() => this.props.closeModal()}
          />
        );

      case "Blocco di testo":
        return (
          <InputText
            manageColumnElements={info => this.props.manageColumnElements(info)}
            modalInfo={this.props.modalInfo}
            selectModal={info => this.props.selectModal(info)}
            closeModal={() => this.props.closeModal()}
          />
        );

      case "Titolo":
        return (
          <TitleText
            manageColumnElements={info => this.props.manageColumnElements(info)}
            modalInfo={this.props.modalInfo}
            selectModal={info => this.props.selectModal(info)}
            closeModal={() => this.props.closeModal()}
          />
        );

      case "Immagine":
        return (
          <InsertImage
            manageColumnElements={info => this.props.manageColumnElements(info)}
            modalInfo={this.props.modalInfo}
            selectModal={info => this.props.selectModal(info)}
            closeModal={() => this.props.closeModal()}
          />
        );

      default:
    }
  }

  render() {
    let modal = this.chooseModal(this.props.modalSelected);
    return (
      <div>
        <div className="absolute z-30 left-0 bg-gray-800 top-0 opacity-50 w-full h-full"></div>
        <div className="z-50 absolute p-4 left-0 w-full flex justify-center">
          {modal}
        </div>
      </div>
    );
  }
}

export default ModalManager;
