import React from "react";
import classNames from "classnames";
import PreviewTitleInput from "./Previews/PreviewTitleInput.js";
import ImagePreview from "./Previews/ImagePreview.js";

class Elements extends React.Component {
  putTitle(info) {
    const del = info =>
      this.props.manageColumnElements({
        operation: "delete",
        indexContainer: this.props.indexContainer,
        indexColonna: this.props.indexColonna,
        indexElement: this.props.indexElements
      });
    const copy = info =>
      this.props.manageColumnElements({
        operation: "copy",
        indexContainer: this.props.indexContainer,
        indexColonna: this.props.indexColonna,
        indexElement: this.props.indexElements
      });
    switch (info) {
      case "InputText":
        return (
          <PreviewTitleInput
            deleteColumnElements={del}
            copyColumnElements={copy}
            selectText={info =>
              this.props.selectModal({
                selectedModal: "Blocco di testo",
                info: {
                  indexContainer: this.props.indexContainer,
                  indexColonna: this.props.indexColonna,
                  indexElement: this.props.indexElements,
                  info: this.props.context
                }
              })
            }
            title="Testo"
            img="text.png"
            text={this.props.context.text}
          />
        );
      case "TitleText":
        return (
          <PreviewTitleInput
            deleteColumnElements={del}
            copyColumnElements={copy}
            selectTitle={info =>
              this.props.selectModal({
                selectedModal: "Titolo",
                info: {
                  indexContainer: this.props.indexContainer,
                  indexColonna: this.props.indexColonna,
                  indexElement: this.props.indexElements,
                  info: this.props.context
                }
              })
            }
            title="Titolo"
            img="title.svg"
            text={this.props.context.text}
          />
        );
      case "InsertImage":
        return (
          <ImagePreview
            selectModal={info =>
              this.props.selectModal({
                selectedModal: "Immagine",
                info: {
                  indexContainer: this.props.indexContainer,
                  indexColonna: this.props.indexColonna,
                  indexElement: this.props.indexElements,
                  info: this.props.context
                }
              })
            }
            deleteColumnElements={del}
            copyColumnElements={copy}
            url={this.props.context.url}
          />
        );
      default:
    }
  }
  render() {
    let arrow = classNames({
      "w-6 h-6 hover:opacity-100 cursor-pointer opacity-75 p-1  flex items-center justify-center": true
    });
    let name = this.putTitle(this.props.context.type);
    return (
      <div className="mb-3 relative">
        <div className="absolute h-full flex justify-center items-center nav-arrow">
          <div className="flex flex-col">
            <div
              onClick={info =>
                this.props.manageColumnElements({
                  indexContainer: this.props.indexContainer,
                  indexColonna: this.props.indexColonna,
                  indexElement: this.props.indexElements,
                  operation: "swap",
                  swap: "before"
                })
              }
              className={arrow}
            >
              <img
                className="w-3 opacity-50"
                src="./Img/icon/arrow_up.png"
                alt="arrow left"
              />
            </div>

            <div
              onClick={info =>
                this.props.manageColumnElements({
                  indexContainer: this.props.indexContainer,
                  indexColonna: this.props.indexColonna,
                  indexElement: this.props.indexElements,
                  operation: "swap",
                  swap: "after"
                })
              }
              className={arrow}
            >
              <img
                className="w-3 opacity-50"
                src="./Img/icon/arrow_down.png"
                alt="arrow right"
              />
            </div>
          </div>
        </div>
        {name}
      </div>
    );
  }
}
export default Elements;
