import React from "react";
import classNames from "classnames";

class Hbutton extends React.Component {
  render() {
    let style = classNames({
      "bg-blue-500 text-white hover:bg-blue-300 font-semibold hover:text-white py-1 mr-2 px-2 border border-blue-500 hover:border-transparent rounded focus:outline-none":
        this.props.value === this.props.choosen,
      "bg-transparent hover:bg-blue-300 text-blue-400 font-semibold hover:text-white py-1 mr-2 px-2 border border-blue-500 hover:border-transparent rounded focus:outline-none":
        this.props.value !== this.props.choosen
    });
    return (
      <button
        onClick={i => this.props.handleHbutton(this.props.value)}
        className={style}
      >
        H{this.props.value}
      </button>
    );
  }
}

class TitleText extends React.Component {
  constructor(props) {
    super(props);
    this.state = { value: "", choosen: 1 };
  }

  componentDidMount() {
    let field = !this.props.modalInfo.info
      ? ""
      : this.props.modalInfo.info.text;
    let index = !this.props.modalInfo.info
      ? 1
      : this.props.modalInfo.info.htype;
    this.setState({
      value: field,
      choosen: index
    });
  }

  componentDidUpdate(prevProps) {
    // Typical usage (NEVER forget to compare props):
    if (this.props.modalInfo.info !== prevProps.modalInfo.info) {
      this.setState({
        value: this.props.modalInfo.info.text,
        choosen: this.props.modalInfo.info.htype
      });
    }
  }

  handleNameChange = event => {
    this.setState({ value: event.target.value });
  };

  handleHbutton(i) {
    this.setState({ choosen: i });
  }

  renderButton() {
    let array = [];
    for (let i = 1; i <= 6; i++) {
      array.push(
        <Hbutton
          key={i}
          value={i}
          choosen={this.state.choosen}
          handleHbutton={i => this.handleHbutton(i)}
        />
      );
    }
    return array;
  }

  updateSend() {
    const add = (
      <button
        onClick={info => {
          this.props.manageColumnElements({
            operation: "add",
            indexContainer: this.props.modalInfo.indexContainer,
            indexColonna: this.props.modalInfo.indexColonna,
            value: {
              type: "TitleText",
              url: this.state.value,
              text: this.state.value,
              htype: this.state.choosen
            }
          });
          this.props.closeModal();
        }}
        className="mt-4 bg-transparent hover:bg-blue-500 text-blue-700 font-semibold hover:text-white py-2 px-4 border border-blue-500 hover:border-transparent rounded"
      >
        Invia
      </button>
    );

    const update = (
      <button
        onClick={info => {
          this.props.manageColumnElements({
            operation: "update",
            indexContainer: this.props.modalInfo.indexContainer,
            indexColonna: this.props.modalInfo.indexColonna,
            indexElement: this.props.modalInfo.indexElement,
            value: {
              type: "TitleText",
              text: this.state.value,
              htype: this.state.choosen
            }
          });
          this.props.closeModal();
        }}
        className="mt-4 bg-transparent hover:bg-blue-500 text-blue-700 font-semibold hover:text-white py-2 px-4 border border-blue-500 hover:border-transparent rounded"
      >
        Aggiorna
      </button>
    );
    return !this.props.modalInfo.info ? add : update;
  }
  render() {
    const button = this.updateSend();
    const Hbuttons = this.renderButton();
    const style = classNames({
      "h-40 shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline": true,
      "text-3xl": this.state.choosen === 1,
      "text-2xl": this.state.choosen === 2,
      "text-xl": this.state.choosen === 3,
      "text-lg": this.state.choosen === 4,
      "text-base": this.state.choosen === 5,
      "text-sm": this.state.choosen === 6
    });
    return (
      <div className="w-full lg:w-3/4">
        <div className="">
          <div className="bg-blue-500 py-2 border-b border-blue-600 shadow-lg">
            <h1 className="text-white text-center font-bold uppercase ">
              Inserisci il Titolo
            </h1>
          </div>
          <div className="bg-white p-4">
            <div>
              <div className="flex mb-3">{Hbuttons}</div>
              <input
                className={style}
                type="text"
                value={this.state.value}
                onChange={this.handleNameChange}
              />
              <div className="flex justify-between">
                {button}
                {/* <button
                  onClick={info => {
                    this.props.manageColumnElements({
                      operation: "add",
                      indexContainer: this.props.modalInfo.indexContainer,
                      indexColonna: this.props.modalInfo.indexColonna,
                      value: {
                        type: "TitleText",
                        text: this.state.value,
                        htype: this.state.choosen
                      }
                    });
                    this.props.closeModal();
                  }}
                  className="mt-4 bg-transparent hover:bg-blue-500 text-blue-700 font-semibold hover:text-white py-2 px-4 border border-blue-500 hover:border-transparent rounded"
                >
                  Invia
                </button> */}
                <button
                  onClick={() => this.props.closeModal()}
                  className="mt-4 bg-transparent hover:bg-blue-500 text-blue-700 font-semibold hover:text-white py-2 px-4 border border-blue-500 hover:border-transparent rounded"
                >
                  Annulla
                </button>
              </div>
            </div>
            <div></div>
          </div>
        </div>
      </div>
    );
  }
}

export default TitleText;
