import React from "react";
/* import classNames from "classnames"; */

class PreviewTitleInput extends React.Component {
  render() {
    let url = "./Img/icon/" + this.props.img;
    return (
      <div className="border border-gray-200 shadow-md">
        <div className="flex justify-between">
          <div className="bg-gray-200 border border-gray-500 px-2 py-1 flex items-center justify-center">
            <img className="w-4 h-4 mr-0 md:mr-2" alt="icon" src={url} />
            <span className="hidden md:block">{this.props.title}</span>
          </div>
          <div className="flex">
            {this.props.title === "Titolo" ? (
              <button
                onClick={info => this.props.selectTitle()}
                className="bg-gray-200 hover:bg-gray-500 w-6 h-6 flex items-center border border-gray-600  justify-center"
              >
                <img
                  className="w-3 opacity-50"
                  src="./Img/icon/modify.png"
                  alt="modify"
                />
              </button>
            ) : (
              <button
                onClick={info => this.props.selectText()}
                className="bg-gray-200 hover:bg-gray-500 w-6 h-6 flex items-center border border-gray-600  justify-center"
              >
                <img
                  className="w-3 opacity-50"
                  src="./Img/icon/modify.png"
                  alt="modify"
                />
              </button>
            )}
            <button
              onClick={info => this.props.copyColumnElements()}
              className="bg-gray-200 hover:bg-gray-500 w-6 h-6 flex items-center border border-gray-600  justify-center"
            >
              <img
                className="w-3 opacity-50"
                src="./Img/icon/copy_black.png"
                alt="copy"
              />
            </button>
            <button
              onClick={() => this.props.deleteColumnElements()}
              className="bg-gray-200 hover:bg-gray-500 w-6 h-6 flex items-center border border-gray-600  justify-center"
            >
              <img
                className="w-3 opacity-50"
                src="./Img/icon/bin_black.png"
                alt="bin"
              />
            </button>
          </div>
        </div>
        <div className="mt-2">
          <div className=" flex flex-col items-center justify-center  p-1 ">
            <span className="text-gray-600 text-xs md:text-base lg:text-xl italic">
              {this.props.text.slice(0, 140)} ...
            </span>
          </div>
        </div>
      </div>
    );
  }
}

export default PreviewTitleInput;
